<!--  -->
<template>
  <div class>
    <el-dialog :title="$t('adv.')" width="40%" :visible.sync="data.show">
      <div>
        <el-form ref="form" :model="data.data" label-width="80px">
          <el-form-item :label="$t('adv.')">
            <el-input v-model="data.data.title"></el-input>
          </el-form-item>
          <el-form-item :label="$t('adv.')">
            <el-radio-group v-model="data.data.status">
              <el-radio :label="1"> {{$t('adv.')}} </el-radio>
              <el-radio :label="2">{{$t('adv.')}}</el-radio>
            </el-radio-group>
          </el-form-item>
          <el-form-item :label="$t('adv.')">
            <el-radio-group v-model="data.data.rType">
              <el-radio :label="1">{{$t('adv.')}}</el-radio>
              <el-radio :label="2">{{$t('adv.()')}}</el-radio>
            </el-radio-group>
          </el-form-item>
          <el-form-item :label="$t('adv.')">
            <div style="border: 1px solid #ccc;">
              <quill-editor
                ref="myQuillEditor"
                v-model="data.data.content"
                :options="options"
                @blur="onEditorBlur($event)"
                @focus="onEditorFocus($event)"
                @ready="onEditorReady($event)"
              />
              <input
                type="file"
                name="file"
                id="uploadEditorImg"
                accept="image/jpeg, image/png, image/gif"
                style="display: none;"
                multiple
                @change="uploadEditorImgFile($event,)"
              />
            </div>
          </el-form-item>
        </el-form>
      </div>
      <span slot="footer" class="dialog-footer">
        <el-button @click="cancel">  {{$t('adv.')}} </el-button>
        <el-button type="primary" @click="handleEnter">  {{$t('adv.')}}</el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
//jsjsjson
//import  from '';
// import hljs from 'highlight.js'
// import "quill/dist/quill.core.css"
// import "quill/dist/quill.snow.css"
// import "quill/dist/quill.bubble.css"
// import { quillEditor } from 'vue-quill-editor'
// import { ImageExtend } from 'quill-image-resize-module';

// Quill.register('modules/ImageExtend', ImageExtend);
import hljs from 'highlight.js'
import {
  eeSourceBtn
} from '@/components/quill.eeSourceBtn.js'
Quill.register('modules/eeSourceBtn', eeSourceBtn)
import "quill/dist/quill.core.css"
import "quill/dist/quill.snow.css"
import "quill/dist/quill.bubble.css"
import {
  Quill,
  quillEditor
} from "vue-quill-editor"
export default {
  //import
  components: { quillEditor },
  props: ['data'],
  data () {
    // 
    return {
      dialogVisible: false,
      content: '<h2>I am Example</h2>',
      options: { //
        modules: {
          syntax: {
            highlight: (text) => hljs.highlightAuto(text).value,
          },
          eeSourceBtn: {},
          toolbar: {
            container: [
              ["bold", "italic", "underline", "strike"], // toggled buttons
              ["blockquote", "code-block"],
              [{
                header: 1
              }, {
                header: 2
              }], // custom button values
              [{
                list: "ordered"
              }, {
                list: "bullet"
              }],
              [{
                script: "sub"
              }, {
                script: "super"
              }], // superscript/subscript
              [{
                indent: "-1"
              }, {
                indent: "+1"
              }], // outdent/indent
              [{
                direction: "rtl"
              }], // text direction
              [{
                size: ["small", false, "large", "huge"]
              }], // custom dropdown
              [{
                header: [1, 2, 3, 4, 5, 6, false]
              }],
              [{
                color: []
              }, {
                background: []
              }], // dropdown with defaults from theme
              [{
                font: []
              }],
              [{
                align: []
              }],
              ["link", "image",], // "video", "formula",
            ],
            handlers: {
              'image': value => {
                document.getElementById('uploadEditorImg').click()
              }
            }
          },
        },
        placeholder: '',
      },

    }
  },
  // data
  computed: {
    //   editor () {
    //   return this.$refs.myQuillEditor.quill
    // }
  },
  //data
  watch: {},
  //
  methods: {
    onEditorBlur (quill) {
      console.log('editor blur!', quill)
       document.querySelector('.ql-editor').setAttribute('data-placeholder', $t('adv.'))
       
    },
    onEditorFocus (quill) {
       document.querySelector('.ql-editor').removeAttribute('data-placeholder', '')
    },
    onEditorReady (quill) {
      console.log('editor ready!', quill)
    },
    handleEnter () {
        console.log(this.data.data);
      let params = {
        id: this.data.data.id,
        content: this.data.data.content,
        rType:this.data.data.rType,
        status:this.data.data.status 
      }
      this.$apiCall("api.VendorShop.vendorChangeProducts", params, r => {
        if (r.ErrorCode == 9999) {
              this.$message.success(r.message)
              this.data.show = false
          } 
      })
    },
    cancel(){
        this.reset()
        this.data.show = false
    },
    reset(){
      this.data = {
        show: false,
        data: {
          rType: 1,
          status: 1,
          content: '',
          title: '',
        }
      }
    },
    uploadEditorImgFile ($event) { //2   210902
      //
      if (!$event.target.files[0].size) return
      this.fileList2($event.target)
      $event.target.value = ""
    },
    fileList2 (el) {
      let files = el.files
      for (let i = 0; i < files.length; i++) {
        if (files[i].size > 2097152) {
          this.$message.error(`${ $t('adv.')}2MB`)
          files.splice(i, 1)
          i--
          return
        }
        let file = files[i]
        if (file.type.indexOf("image") > -1) { //
          let reader = new FileReader()
          reader.readAsDataURL(file)
          reader.onload = (e) => {
            this.uploadBase64(e.target.result)
          }
        } else {
          this.$message({
            message: '',
            type: "error"
          })
        }
      }
    },
    uploadBase64 (base64) {
      let imgUrlBase64 = base64.split("base64,").pop()
      let ext = base64.match(/data:image\/(.*);base64,.*/)[1]
      let params = {
        imgUrlBase64,
        ext,
      }
      this.loading = true
      this.$apiCall('api.Comment.uploadImg', params, r => {
        this.loading = false
        if (r.ErrorCode == 9999) {
          let url = r.Data.Results.imgUrl
          // 
          let quill = this.$refs.goodsEditor.quill
          // 
          let length = quill.getSelection().index
          // quillarr 
          quill.insertEmbed(length, 'image', url)
          quill.setSelection(length + 1)
        } else {
          this.$message({
            message: r.Message,
            type: "error"
          })
        }
      })
    }
  },
  // - this
  created () {

  },
  // - DOM
  mounted () {
    console.log('this is current quill instance object', this.editor)
  },
  beforeCreate () { }, // - 
  beforeMount () { }, // - 
  beforeUpdate () { }, // - 
  updated () { }, // - 
  beforeDestroy () { }, // - 
  destroyed () { }, // - 
  activated () { }, //keep-alive
}
</script>
<style  scoped>
</style>
<style>
.ql-container {
  height: 260px;
}
</style>