<!--  -->
<template>
  <div class>
    <el-card class="box-card" style="margin-bottom:20px">
      <div>
        <el-button type="primary" @click="handleShow"></el-button>
      </div>
    </el-card>
    <el-card class="box-card" v-loading="isLoading">
      <el-table :data="tableData" border style="width: 100%">
        <el-table-column prop="title" :label="$t('adv.')" align="center" width="180"></el-table-column>
        <el-table-column prop="status" :label="$t('adv.')" width="180" align="center">
          <template slot-scope="scope">
            <span v-if="scope.row.status == 1">{{$t('adv.')}}</span>
            <span v-else>{{$t('adv.')}}</span>
          </template>
        </el-table-column>
        <el-table-column prop="rType" :label="$t('adv.')" align="center">
          <template slot-scope="scope">
            <span v-if="scope.row.rType == 1">{{$t('adv.')}}</span>
            <span v-else>{{$t('adv.')}}</span>
          </template>
        </el-table-column>
        <el-table-column prop="updatedAt" :label="$t('adv.')" align="center"></el-table-column>
        <el-table-column prop="address" :label="$t('adv.')" align="center">
          <template slot-scope="scope">
            <el-link type="primary" @click="openEdit(scope.row)">{{$t('adv.')}}</el-link>
            <br/>
            <el-link type="danger" @click="delRow(scope.row.id)">{{$t('adv.')}}</el-link>
          </template>
        </el-table-column>
      </el-table>
    </el-card>
    <createAdv :data="dataDialog" @black="updatedList"/>
  </div>
</template>

<script>
//jsjsjson
//import  from '';
import createAdv from './createAdv.vue'
export default {
  //import
  components: { createAdv },
  data () {
    //
    return {
      tableData: [],
      isLoading: false,
      dataDialog: {
        show: false,
        data: {
          id: '',
          title: '',
          content: '',
          rType: '1',
          status: '1',
        }
      }
    }
  },
  // data
  computed: {},
  //data
  watch: {},
  //
  methods: {
    handleShow () {
      this.dataDialog.show = true
    },
    openEdit(row){
        this.dataDialog = {
          show : true,
          data:{
            id: row.id,
            title: row.title,
            content: row.content,
            rType: row.rType,
            status: row.status,
          }
        }
    },
    delRow(id){
      this.$apiCall("api.Notice.delete", {id:id}, r => {
        this.isLoading = false
        if (r.ErrorCode == 9999) {
            this.$message.success(r.Message)
             this.getAdvList ()
        }else{
           this.$message.error(r.Message)
        }
      })
    },
    updatedList(){
      this.getAdvList()
      this.reset()
    },
  reset(){
      this.dataDialog = {
        show: false,
        data: {
          rType: '1',
          status: '1',
          content: '',
          title: '',
        }
      }
    },
    getAdvList () {
      this.isLoading = true
      this.$apiCall("api.Notice.finds", {}, r => {
        this.isLoading = false
        if (r.ErrorCode == 9999) {
          this.tableData = r.Data.Results
        }else{
           this.$message.error(r.Message)
        }
      })
    }
  },
  // - this
  created () {
    this.getAdvList()
  },
  // - DOM
  mounted () {

  },
  beforeCreate () { }, // - 
  beforeMount () { }, // - 
  beforeUpdate () { }, // - 
  updated () { }, // - 
  beforeDestroy () { }, // - 
  destroyed () { }, // - 
  activated () { }, //keep-alive
}
</script>
<style  scoped>
</style>